// Color Pallete
$white: #fff;
$light: #f8f9fa;
$dark: #343a40;
$black: #000;
$blue: #007bff;
$blue-light: #007bff10;
$blue-hover: #007bff30;
$blue-light-opaque: #eff7ff;
$blue-mid: #007bff99;
$pblue: #4c75f2;
$pblue-light: #4c75f210;
$pblue-hover: #4c75f230;
$pblue-light-opaque: #eff7ff;
$pblue-mid: #4c75f299;
$cherry: #ff073a;
$cherry-mid: #ff073a99;
$cherry-light: #ff073a20;
$cherry-hover: #ff073a30;
$cherry-light-opaque: #ffe0e6;
$red: #dc3545;
$red-light: #dc354520;
$red-mid: #dc354599;
$red-hover: #dc354530;
$orange: #fd7e14;
$orange-mid: #fd7e1499;
$orange-light: #fd7e1420;
$orange-hover: #fd7e1430;
$orange-light-opaque: #ffefe2;
$yellow: #ffc107;
$yellow-light: #ffc10720;
$yellow-hover: #ffc10730;
$yellow-mid: #ffc10799;
$yellow-light-opaque: #fff7e0;
$green: #28a745;
$green-light: #28a74520;
$green-hover: #28a74530;
$green-mid: #28a74599;
$gray: #6c757d;
$gray-light: #6c757d10;
$gray-light-opaque: #f6f6f7;
$gray-hover: #6c757d20;
$gray-mid: #6c757d99;
$gray-middark: #6c757d30;
$gray-dark: #343a40;
$gray-opaque: #f1f1f1;
$gray-hover-opaque: #edeeef;
$gray-hover-darker-opaque: #f6f6f7;
$purple: #201aa2dd;
$purple-light: #201aa220;
$purple-hover: #201aa230;
$purple-mid: #201aa299;
$purple-light-opaque: #e3e2f3;
$pink: #fb5581;
$pink-light: #ffa8cb30;
$pink-hover: #ffa8cb60;
$pink-mid: #fb558199;
$brown: #b6854d;
$brown-light: #b6854d10;
$brown-hover: #b6854d30;
$brown-mid: #b6854d99;
$brick: #e23028;
$brick-light: #e2302810;
// Dark mode variables
$dark-m-gray: #161625;
$dark-mid-subtext: #bdb8ae;
$dark-m-white: #e1e1e1;
$dark-m-blue: #3391ff;
$dark-m-purple: #9673b9;
$dark-m-purple-mid: #9673b9bb;
$dark-m-purple-light: #40008050;
* {
    font-family: 'archia';
}

html {
    overflow-x: hidden;
    width: 100vw;
}

body {
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    margin: 0;
    padding: 0;
}

h1 {
    font-size: 24px !important;
    font-weight: 900;
}

h2 {
    font-size: 18px !important;
    font-weight: 400;
}

h3 {
    font-size: 14px !important;
    font-weight: 400;
}

h4 {
    font-size: 13px !important;
    font-weight: 900;
}

h5 {
    font-size: 12px !important;
    font-weight: 900;
}

h6 {
    font-size: 11px !important;
    font-weight: 600;
}

.App {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.arrow-up {
    border-bottom: 5px solid $gray-mid;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    margin-left: 0.25rem;
}

.arrow-down {
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-top: 5px solid $gray-mid;
    margin-left: 0.25rem;
}

.Navbar {
    align-items: center;
    display: flex;
    flex-direction: row;
    height: 4rem;
    justify-content: space-between;
    a {
        color: inherit;
        text-decoration: none;
    }
    .navbar-left {
        color: $gray-mid;
        cursor: pointer;
        font-size: 12px;
        font-weight: 600;
        padding: 1.5rem;
        text-align: center;
        width: 3rem;
        &:hover {
            color: $gray;
        }
    }
    .navbar-middle {
        color: $gray;
        font-size: 18px;
        font-weight: 900;
        text-transform: uppercase;
        span {
            color: $pblue;
        }
    }
    .navbar-right {
        color: $gray;
        cursor: pointer;
        font-size: 12px;
        font-weight: 600;
        padding: 1.5rem;
        text-align: center;
        width: 3rem;
    }
    .language-toggle {
        cursor: pointer;
        font-weight: 900;
        text-transform: uppercase;
    }
    .expand {
        background: $gray-light-opaque;
        color: $gray;
        display: flex;
        flex-direction: column;
        width: 100vw;
        z-index: 9999;
        &>* {
            border-bottom: 1px solid $gray-light;
            border-top: 1px solid $gray-light;
            padding: 1.5rem;
            transition: all 0.15s ease-in-out;
            &:hover {
                background: $gray-hover;
            }
            span {
                &.focused {
                    background: $pblue-light;
                    color: $pblue;
                    padding: 0.25rem;
                }
            }
        }
        .expand-bottom {
            align-self: flex-start;
            border: 0;
            display: flex;
            flex-direction: column;
            justify-content: center;
            h5 {
                margin: 0;
            }
            &:hover {
                background: none;
            }
        }
    }
}

.Home {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: 9rem;
    margin-right: 3rem;
    .home-left,
    .home-right {
        display: flex;
        flex-direction: column;
        width: 34rem;
    }
    .home-left {
        margin-right: 2.5rem;
        min-height: 60rem;
    }
    .home-right {
        margin-left: 2.5rem;
        min-height: 10rem;
    }
    .header {
        z-index: 99;
    }
}

.alert {
    align-items: flex-start;
    background: $gray-light;
    border-radius: 5px;
    color: $gray;
    display: flex;
    flex-direction: row;
    font-size: 10px;
    padding: 0.5rem;
    svg {
        color: $gray-mid;
        height: 16px;
        width: 16px;
    }
    .alert-right {
        margin-left: .25rem;
        text-align: right;
        width: 9rem;
        a {
            background: $gray-light;
            color: $gray;
            padding-left: 0.1rem;
            padding-right: 0.1rem;
            &:hover {
                background: $gray-hover;
            }
        }
        &.is-full {
            width: 100%;
        }
    }
    &.is-green {
        background: $green-light;
        color: $green;
        svg {
            stroke: $green-mid;
        }
    }
}

.StateHeader {
    align-self: center;
    display: flex;
    flex-direction: row;
    height: 5.25rem;
    justify-content: space-between;
    margin-bottom: 1rem;
    position: relative;
    width: 100%;
    &>* {
        align-self: flex-start;
    }
    .header-left {
        h1,
        h5 {
            margin: 0;
        }
        h1 {
            color: $brick;
            max-width: 20rem;
        }
        h5 {
            color: $gray;
            font-weight: 600;
            margin-top: .5rem;
        }
    }
    .header-right {
        color: $purple-mid;
        display: flex;
        flex-direction: column;
        text-align: right;
        h2,
        h5 {
            font-weight: 600;
            margin: 0;
        }
        h2 {
            color: $purple;
            font-weight: 900;
        }
        a {
            background: $purple-light;
            color: inherit;
            text-decoration: none;
            &:hover {
                background: $purple-hover;
            }
        }
    }
}

.State {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: 8rem;
    margin-right: 3rem;
    .state-left,
    .state-right {
        display: flex;
        flex-direction: column;
        width: 34rem;
    }
    .state-left {
        margin-right: 2.5rem;
        position: relative;
    }
    .state-right {
        margin-left: 2.5rem;
    }
    a {
        text-decoration: none;
    }
    .district-bar {
        width: calc(100% - 2rem);
    }
    .Minigraph {
        .svg-parent {
            width: calc(25%) !important;
        }
    }
    .MapExplorer {
        .header,
        .map-stats,
        .map-button,
        .tabs-map,
        .last-update,
        .footnote {
            display: none;
        }
        .anchor {
            display: none;
        }
    }
    .TimeseriesExplorer {
        .anchor {
            display: none;
        }
    }
}

.StateMeta {
    align-self: center;
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 3rem;
    width: calc(100% - 3rem);
    &.population {
        display: flex !important;
        flex-direction: row !important;
        justify-content: space-between !important;
        margin: 0;
        margin-bottom: 2.5rem;
        margin-top: 2.5rem;
        .alert {
            margin: 0;
        }
    }
    .meta-item {
        border-radius: 5px;
        min-height: 6rem;
        padding-left: 1rem;
        padding-top: 1rem;
        h1,
        h2,
        h3,
        h4,
        h5 {
            margin: 0;
        }
        h5 {
            font-weight: 600;
        }
        h2 {
            font-weight: 600;
        }
        h3 {
            font-weight: 600;
            width: 10rem;
            word-wrap: break-word;
        }
        h1 {
            display: inline-block;
            font-weight: 600;
        }
        &.population {
            min-height: 1rem !important;
            padding: 0 !important;
            h1,
            h3 {
                color: $gray;
            }
        }
        .meta-item-top {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            &>* {
                align-self: center;
            }
            h3 {
                width: calc(100% - 2.5rem);
            }
            span {
                margin-bottom: auto;
                margin-right: 1rem;
            }
            svg {
                align-self: flex-start;
                cursor: pointer;
                stroke-width: 2.5px;
                width: 16px;
            }
        }
        .meta-item-middle {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            * {
                align-self: center;
            }
            svg {
                cursor: pointer;
                margin-right: 1rem;
            }
        }
        p {
            font-weight: 600;
            margin-bottom: 1.25rem;
            padding-right: 1rem;
        }
        &.confirmed {
            background: $cherry-light;
            h3,
            h5,
            svg,
            p {
                color: $cherry-mid;
            }
            h1 {
                color: $cherry;
            }
        }
        &.active {
            background: $blue-light;
            h3,
            h5,
            svg,
            p {
                color: $blue-mid;
            }
            h1 {
                color: $blue;
            }
        }
        &.recovery {
            background: $green-light;
            h3,
            h5,
            svg,
            p {
                color: $green-mid;
            }
            h1 {
                color: $green;
            }
        }
        &.mortality {
            background: $gray-light;
            h3,
            h5,
            svg,
            p {
                color: $gray-mid;
            }
            h1 {
                color: $gray;
            }
        }
        &.cpm {
            background: $yellow-light;
            h3,
            h5,
            svg {
                color: $orange-mid;
            }
            h1 {
                color: $orange;
            }
        }
        &.tpm {
            background: $purple-light;
            h3,
            h5,
            svg,
            p {
                color: $purple-mid;
            }
            h1 {
                color: $purple;
            }
        }
        &.ptr {
            background: $pink-light;
            h3,
            h5,
            svg,
            p {
                color: $pink-mid;
            }
            h1 {
                color: $pink;
            }
        }
        &.gr {
            background: $brown-light;
            h3,
            h5,
            p,
            svg {
                color: $brown-mid;
            }
            h1 {
                color: $brown;
            }
        }
    }
}

.StateDropdown {
    align-items: center;
    display: flex;
    flex-direction: row;
    position: relative;
    .state-name {
        background: $brick-light;
        border-radius: 5px;
        cursor: pointer;
        padding: .25rem;
    }
    .dropdown {
        background: $white;
        border-radius: 5px;
        height: 25rem;
        left: -.25rem;
        overflow-y: scroll;
        position: absolute;
        top: 2.75rem;
        width: 15rem;
        z-index: 999;
        .item {
            color: $red;
            cursor: pointer;
            padding: 0.75rem;
            &:hover {
                background: $red-hover;
            }
        }
    }
    .backdrop {
        background: $red-light;
        height: 100vh;
        left: 0;
        pointer-events: none;
        position: fixed;
        top: 0;
        width: 100vw;
        z-index: 10;
    }
}

.labels {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 1rem;
    .label {
        align-items: center;
        background: $gray-light !important;
        border-radius: 5px !important;
        color: $gray-mid !important;
        cursor: pointer;
        display: flex;
        flex-direction: row;
        font-size: 14px;
        margin-bottom: 0.25rem;
        margin-right: .25rem;
        padding: .25rem .5rem;
        text-transform: capitalize;
        &.is-selected {
            background: $yellow-hover !important;
            color: $orange-mid !important;
            &:hover {
                background: $yellow-hover !important;
                color: $orange-mid !important;
            }
        }
        @media not all and (pointer: coarse) {
            &:hover {
                background: $yellow-light !important;
                color: $orange-mid !important;
            }
        }
        .count {
            align-items: center;
            background: inherit;
            border-radius: 50%;
            color: inherit;
            display: flex;
            flex-direction: row;
            font-size: 12px;
            height: 1.5rem;
            justify-content: center;
            margin-left: 0.25rem;
            width: 1.5rem;
        }
    }
}

.Banner {
    background: $purple-light;
    color: $purple;
    display: flex;
    flex-direction: row;
    font-size: 0.8rem;
    font-weight: 600;
    justify-content: center;
    line-height: 20px;
    .snippet {
        align-self: center;
        cursor: pointer;
        padding: 1.5rem;
        text-align: center;
        width: calc(100% - 10rem);
        word-wrap: break-word;
    }
}

.Search {
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
    position: relative;
    width: 100%;
    &>* {
        align-self: center;
    }
    label {
        color: $gray;
        font-size: 0.75rem;
        margin-bottom: 1rem;
    }
    .line {
        background: $gray-light;
        height: 1rem;
        width: 2px;
    }
    input {
        -moz-box-shadow: 0 10px 35px rgba(0, 0, 0, 0.1);
        -webkit-appearance: none !important;
        -webkit-box-shadow: 0 10px 35px rgba(0, 0, 0, 0.1);
        background: #fff;
        border: 0;
        border-radius: 5px;
        box-shadow: 0 10px 35px rgba(0, 0, 0, 0.1);
        color: $gray;
        font-size: 1rem;
        height: 2rem;
        outline: none;
        padding: 1rem;
        padding-left: 3rem;
        width: calc(100% - 4rem);
    }
    .search-input-wrapper {
        position: relative;
        width: 100%;
    }
    .search-placeholder {
        color: $gray-mid;
        font-size: 1rem;
        left: 3rem;
        opacity: 0.8;
        pointer-events: none;
        position: absolute;
        top: 1.4rem;
        transition: opacity 1s;
        &.disappear {
            opacity: 0;
        }
    }
    .search-button {
        svg {
            color: $gray-mid;
            left: 0.75rem;
            position: absolute;
            top: 1.25rem;
            transition: all 0.2s ease-in-out;
        }
        &.is-expand {
            svg {
                left: 0.5rem;
            }
        }
    }
    .close-button {
        background: $gray-light !important;
        border-radius: 50%;
        cursor: pointer;
        display: flex;
        flex-direction: row;
        height: 20px;
        justify-content: center;
        position: absolute;
        right: 1rem;
        top: 1.35rem;
        transition: all 0.2s ease-in-out;
        width: 20px;
        &:hover {
            background: $gray-hover !important;
        }
        &.is-expand {
            right: 0.75rem;
        }
        svg {
            align-self: center;
            position: absolute;
            stroke: $gray-mid;
            stroke-width: 3;
            width: 12px;
        }
        &.custom {
            background: $pink-light !important;
            pointer-events: none;
            z-index: 0;
            svg {
                stroke: $pink !important;
            }
        }
    }
    .feature {
        background: $pink-light;
        border-radius: 5px;
        color: $pink;
        cursor: pointer;
        font-size: .75rem;
        padding: .75rem;
        text-align: center;
    }
    .expanded {
        display: flex;
        flex-direction: row;
        margin-top: 2rem;
        padding: 1rem;
        padding-bottom: 0;
        width: calc(100% - 4rem);
        &>* {
            flex: 1;
        }
        h4,
        h3 {
            color: $gray;
            font-weight: 600;
            margin: 0;
            margin-bottom: 0.5rem;
        }
        h3 {
            color: $pblue;
            margin-bottom: 0.75rem;
        }
    }
    .results {
        padding: 1rem;
        padding-bottom: 0;
        width: calc(100% - 2rem);
        a {
            text-decoration: none !important;
        }
        .pan-divider {
            color: $gray;
            font-weight: 600;
            padding: 1rem 0;
        }
        .result,
        .essential-result {
            align-self: flex-start;
            border-bottom: 2px solid $gray-light;
            color: $gray;
            cursor: pointer;
            display: flex;
            flex-direction: row;
            font-size: 0.9rem;
            justify-content: space-between;
            min-height: 2rem;
            padding: 1rem;
            transition: all 0.2s ease-in-out;
            &:hover {
                background: $gray-light;
            }
            &>* {
                align-self: center;
            }
            .result-name {
                max-width: 15rem;
            }
            .result-type {
                background: $yellow-light;
                border-radius: 2px;
                color: $orange;
                display: flex;
                flex-direction: row;
                font-size: 0.75rem;
                padding: 0.25rem;
                &:hover {
                    background: $yellow-hover;
                }
                svg {
                    margin-left: 0.25rem;
                    stroke: $orange-mid;
                    stroke-width: 3px;
                }
                * {
                    align-self: center;
                }
            }
            .result-left {
                display: flex;
                flex-direction: row;
                * {
                    align-self: center;
                }
            }
            .result-zone {
                border-radius: 50%;
                height: 0.75rem;
                margin-left: 0.5rem;
                width: 0.75rem;
                &.is-red {
                    background: $cherry-light;
                    border: 2px solid $cherry-mid;
                }
                &.is-orange {
                    background: $orange-light;
                    border: 2px solid $orange-mid;
                }
                &.is-green {
                    background: $green-light;
                    border: 2px solid $green-mid;
                }
            }
        }
        .essential-result {
            display: flex;
            flex-direction: column;
            .result-top {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                width: 100%;
                .result-top-left {
                    display: flex;
                    flex-direction: column;
                    .result-location {
                        display: flex;
                        flex-direction: row;
                        font-size: 0.75rem;
                    }
                    .result-distance {
                        color: $green;
                        display: flex;
                        flex-direction: row;
                        font-size: 0.75rem;
                    }
                }
            }
            .result-category {
                align-self: flex-start;
                background: $pink-light;
                border-radius: 2px;
                color: $pink;
                display: flex;
                flex-direction: row;
                font-size: 0.75rem;
                height: 1rem;
                padding: 0.25rem;
                text-align: right;
                text-transform: capitalize;
                &:hover {
                    background: $pink-hover;
                }
                &>* {
                    align-self: center;
                }
                svg {
                    margin-left: 0.25rem;
                    stroke-width: 3px;
                    width: 12px;
                }
            }
            .result-description {
                align-self: flex-start;
                font-size: 0.75rem;
                margin-bottom: .5rem;
                margin-top: 1rem;
            }
            .result-contacts {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                width: 100%;
            }
            .result-contact {
                align-self: flex-start;
                background: $gray-light;
                border-radius: 2px;
                color: $gray;
                display: flex;
                flex-direction: row;
                font-size: 0.75rem;
                height: 1rem;
                margin-right: .25rem;
                margin-top: 0.25rem;
                padding: 0.25rem;
                text-align: right;
                &:hover {
                    background: $gray-hover;
                }
                a {
                    color: inherit;
                }
                &>* {
                    align-self: center;
                }
                svg {
                    margin-right: 0.25rem;
                    stroke: $gray-mid;
                    stroke-width: 3px;
                    width: 12px;
                }
            }
        }
    }
    .suggestions {
        display: flex;
        flex-direction: column;
        margin-right: 1rem;
        .suggestion {
            color: $gray-mid;
            display: flex;
            flex-direction: row;
            padding: .1rem;
            div {
                padding-top: 1px;
            }
            h4 {
                cursor: pointer;
                margin-left: 0.25rem;
                word-wrap: wrap;
                &:hover {
                    text-decoration: underline;
                    text-decoration-thickness: 2px;
                }
            }
        }
    }
}

.header {
    align-self: center;
    display: flex;
    flex-direction: column;
    max-width: 40rem;
    width: 100%;
    &>* {
        align-self: center;
    }
    .actions {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        padding: 1rem;
        &.timeline {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            padding: 1rem;
            position: absolute;
            top: 13.25rem;
            .highlight {
                color: $brown !important;
                position: absolute;
                text-align: center;
                top: 2.5rem;
                width: 100%;
            }
        }
        &>* {
            align-self: center;
            margin-left: 0.5rem;
            margin-right: 0.5rem;
        }
        h5 {
            color: $gray;
            font-weight: 600;
            margin: 0;
        }
        svg {
            color: $gray-mid;
            cursor: pointer;
            stroke-width: 3;
            width: 16px;
            &:hover {
                stroke: $gray;
            }
        }
        .timeline-icon {
            display: flex;
            flex-direction: column;
            justify-content: center;
            svg {
                display: flex;
                fill: #a6abb0 !important;
                flex-direction: row;
                justify-content: center;
                stroke: #a6abb0 !important;
                stroke-width: 0.5 !important;
                path {
                    align-self: center;
                }
                &:hover {
                    fill: $gray !important;
                    stroke: $gray !important;
                }
            }
        }
        .bell-icon {
            height: 24px;
            position: relative;
            .indicator {
                background: $pblue;
                border-radius: 50%;
                height: 7px;
                position: absolute;
                right: -3px;
                top: -3px;
                width: 7px;
            }
        }
    }
}

.Timeline {
    cursor: pointer;
    height: 3rem;
    width: 34rem;
    will-change: transform;
    .day {
        position: absolute;
        text-align: center;
        width: 70px;
        will-change: transform;
        h5 {
            color: $gray-mid;
            &:hover {
                color: $gray !important;
            }
            &::selection {
                background: transparent !important;
            }
        }
    }
}

.button {
    background: $blue-light;
    border: 0;
    border-radius: 5px;
    color: $blue-mid;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    font-size: 0.75rem;
    font-weight: 900;
    margin-bottom: 0.5rem;
    margin-top: 0.25rem;
    outline: none;
    padding: 0.5rem;
    text-decoration: none;
    text-transform: uppercase;
    transition: background 0.2s ease-in-out;
    svg {
        padding-right: 6px;
        stroke-width: 2px;
        width: 20px;
    }
    &.is-purple {
        background: $purple-light;
        color: $purple-mid;
        &:hover {
            background: $purple-hover;
        }
    }
    &.is-green {
        background: $green-light;
        color: $green-mid;
        &:hover {
            background: $green-hover;
        }
    }
    &:hover {
        background: $blue-hover;
    }
    &>* {
        align-self: center;
    }
}

.MapSwitcher {
    align-self: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: absolute;
    width: 100%;
    .highlight {
        pointer-events: none;
        position: absolute;
        width: calc(25%) !important;
    }
    .highlight,
    .clickable {
        border-radius: 5px;
        cursor: pointer;
        height: 13rem;
        width: calc(25%);
        z-index: 10;
    }
    .clickable {
        transition: background 0.15s ease-in-out;
        &:hover {
            &.is-confirmed {
                background: $cherry-light;
            }
            &.is-active {
                background: $blue-light;
            }
            &.is-recovered {
                background: $green-light;
            }
            &.is-deceased {
                background: $gray-light;
            }
        }
    }
}

.Level {
    align-self: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    .level-item {
        display: flex;
        flex-direction: column;
        width: calc(25%);
        &>* {
            align-self: center;
        }
    }
    h1,
    h5 {
        margin-bottom: 0;
    }
    h1 {
        font-weight: 600;
    }
    h4 {
        margin-bottom: -0.5rem;
    }
    h5 {
        font-weight: 600;
        overflow-wrap: break-word;
        text-align: center;
        width: 100%;
    }
    .is-confirmed {
        h1,
        h5 {
            color: $cherry;
        }
        h4 {
            color: $cherry-mid;
        }
    }
    .is-active {
        h1,
        h5 {
            color: $blue;
        }
        h4 {
            color: $blue-mid;
        }
    }
    .is-recovered {
        h1,
        h5 {
            color: $green;
        }
        h4 {
            color: $green-mid;
        }
    }
    .is-deceased {
        h1,
        h5 {
            color: $gray;
        }
        h4 {
            color: $gray-mid;
        }
    }
}

abbr {
    text-decoration: none;
    &.is-cherry {
        color: $cherry;
    }
    &.is-blue {
        color: $blue;
    }
    &.is-green {
        color: $green;
    }
    &.is-gray {
        color: $gray;
    }
}

.TableLoader {
    margin-bottom: -.25rem;
}

.table-top {
    align-items: flex-end;
    align-self: center;
    display: flex;
    flex-direction: row;
    font-weight: 600;
    justify-content: space-between;
    margin-bottom: .25rem;
    width: 100%;
    .option-toggle,
    .info-toggle,
    .million-toggle,
    .expand-table-toggle {
        background: $gray-light;
        border-radius: 5px;
        color: $gray-mid;
        cursor: pointer;
        margin-right: .25rem;
        padding: .5rem;
        svg {
            align-self: center;
            margin: .25rem;
        }
        &.is-highlighted {
            background: $green-light;
            color: $green;
        }
    }
    .info-toggle {
        &.is-highlighted {
            background: $yellow-light;
            color: $yellow;
        }
    }
    .million-toggle {
        font-size: 13px;
        font-weight: 600;
        padding: 11.5px 10px;
        text-align: center;
        &.is-highlighted {
            background: $pink-light;
            color: $pink;
        }
    }
    .expand-table-toggle {
        color: $gray-mid;
        margin-left: auto;
        margin-right: 0;
        &.is-highlighted {
            svg {
                transform: rotate(90deg);
            }
        }
        svg {
            cursor: pointer;
            margin-bottom: 0.25rem;
            transform: rotate(-90deg);
        }
    }
    @media (min-width: 769px) {
        margin-top: 1rem;
    }
}

.table-helper {
    align-self: center;
    color: $gray;
    display: flex;
    flex-direction: column;
    width: calc(100% - 2rem);
    h5 {
        color: $gray-mid;
    }
    .helper-top {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    .info-item {
        align-items: center;
        color: $gray;
        display: flex;
        flex-direction: row;
        font-size: 12px;
        font-weight: 600;
        height: 1.5rem;
        &:first-child {
            margin-top: 1rem;
        }
         :first-child {
            width: 1.6rem;
            svg {
                width: 15px;
            }
        }
        p {
            max-width: calc(100% - 1.6rem);
        }
        svg {
            color: $gray-mid;
            margin-bottom: .25rem;
        }
        .is-confirmed {
            svg {
                color: $cherry;
            }
        }
        .is-active {
            svg {
                color: $blue;
            }
        }
        .is-recovered {
            svg {
                color: $green;
            }
        }
        .is-deceased {
            svg {
                color: $gray;
            }
        }
        .is-tested {
            svg {
                color: $purple;
            }
        }
        &.notes {
            svg {
                margin-bottom: 0;
            }
        }
        &.sort {
            svg {
                margin-bottom: 0;
            }
        }
        &.invert {
            svg {
                margin-bottom: 0;
                transform: rotate(180deg);
            }
        }
    }
    .text {
        color: $gray-mid;
        font-weight: 600;
        a {
            background: $gray-light;
            border-bottom: 2px solid $gray-hover;
            color: $gray;
            text-decoration: none;
            &:hover {
                background: $gray-hover;
            }
        }
    }
}

.table-container {
    align-self: center;
    margin-bottom: 2.5rem;
    overflow-x: auto;
    width: 100%;
}

.table {
    border-radius: 3px;
    display: inline-grid;
    font-size: 12px;
    grid-template-rows: auto;
    min-width: 100%;
    .state-meta {
        grid-column: 1 / -1;
        .state-meta-top {
            align-items: center;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-between;
            margin-bottom: 1 + .25rem;
            margin-top: 1rem;
            svg {
                margin-right: .5rem;
            }
            .last-updated {
                align-items: center;
                color: $green;
                display: flex;
                flex-direction: row;
            }
            .state-page {
                align-items: center;
                background: $yellow-light;
                border-radius: 5px;
                color: $orange;
                cursor: pointer;
                display: flex;
                flex-direction: row;
                padding: .75rem;
                p {
                    align-self: flex-end;
                    flex: 1;
                    margin: 0;
                    text-align: right;
                }
                &:hover {
                    background: $yellow-hover;
                }
            }
        }
        .state-meta-bottom {
            padding-bottom: 1.5rem;
            .disclaimer {
                background: $brick-light;
                border-radius: 5px;
                color: $brick;
                margin: auto;
                padding: 1rem .25rem;
                text-align: center;
                width: 16rem;
                svg {
                    margin-right: 0.5rem;
                }
            }
        }
    }
    .row {
        cursor: pointer;
        display: contents;
        width: 100%;
        &:nth-child(odd) {
            .cell {
                background: $gray-light;
            }
        }
        &.is-highlighted {
            .cell {
                background: $gray-hover-opaque !important;
            }
        }
        &.heading {
            .cell {
                cursor: pointer;
                user-select: none;
                &:hover {
                    background: $gray-hover-opaque !important;
                }
            }
        }
        &.is-total {
            position: sticky;
            top: 0;
            .cell {
                background: $gray-hover-opaque !important;
            }
        }
        .cell {
            align-items: center;
            border-radius: 3px;
            color: $gray;
            display: flex;
            flex-direction: row;
            margin-bottom: .25rem;
            padding: .5rem;
            position: relative;
            &:not(:first-child) {
                flex-basis: 4rem;
                flex-grow: 1;
                flex-shrink: 0;
                justify-content: flex-end;
            }
            &:first-child {
                background: $gray-hover-darker-opaque;
                flex-basis: 7rem;
                flex-grow: 0;
                flex-shrink: 0;
                justify-content: space-between;
                left: 0;
                position: sticky;
                z-index: 11;
                .sort-icon {
                    left: unset;
                    right: 0;
                }
            }
            svg {
                color: $gray-mid;
                margin-left: 0.2rem;
                stroke-width: 2.5px;
            }
            .sort-icon {
                left: 0;
                margin-left: .25rem;
                margin-right: .25rem;
                position: absolute;
                top: 0;
                &.is-confirmed {
                    svg {
                        color: $cherry;
                    }
                }
                &.is-active {
                    svg {
                        color: $blue;
                    }
                }
                &.is-recovered {
                    svg {
                        color: $green;
                    }
                }
                &.is-deceased {
                    svg {
                        color: $gray;
                    }
                }
                &.is-tested {
                    svg {
                        color: $purple;
                    }
                }
                &.invert {
                    margin-top: .25rem;
                    transform: rotate(180deg);
                }
            }
            &.statistic {
                align-items: flex-end;
                display: flex;
                flex-direction: column;
                justify-content: flex-end;
                .total {
                    font-size: 14px;
                }
                .is-confirmed {
                    color: $cherry;
                }
                .is-active {
                    color: $blue;
                }
                .is-recovered {
                    color: $green;
                }
                .is-deceased {
                    color: $gray;
                }
                .is-tested {
                    color: $purple;
                }
            }
            &:not(:last-child) {
                margin-right: 0.25rem;
            }
            &.heading {
                background: $gray-light-opaque;
                font-size: 13px;
                font-weight: 900;
                padding: 1.25rem .5rem .75rem;
                &:hover {
                    background: $gray-hover-opaque !important;
                }
            }
            .state-name,
            .district-name {
                font-size: 14px;
                font-weight: 600;
                max-width: 7rem;
                word-wrap: break-word;
            }
        }
    }
    .spacer-row {
        grid-column: 1 / -1;
        .spacer {
            color: $gray-mid;
            margin-bottom: 1 + .25rem;
            margin-top: 1rem;
            text-align: center;
            p {
                margin-left: auto;
                margin-right: auto;
                width: 10rem;
            }
            .fold {
                cursor: pointer;
                &:hover {
                    color: $gray;
                }
            }
        }
    }
    .intersection {
        display: block;
    }
}

#chart {
    z-index: 10;
}

.anchor {
    cursor: pointer;
    position: absolute;
    right: 0;
    transform: rotate(45deg);
    transition: all 0.1s ease-in-out;
    z-index: 999;
    svg {
        color: $gray-mid;
        &:hover {
            color: $gray;
        }
    }
    &.stickied {
        transform: rotate(90deg);
        svg {
            color: $gray;
        }
    }
}

.map-container {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.MapExplorer {
    align-self: center;
    margin-bottom: 3rem;
    position: relative;
    width: 100%;
    &.stickied {
        position: sticky !important;
        top: 0;
    }
    &.hidden {
        display: none;
    }
    .svg-parent {
        display: block;
        margin: auto;
        margin-top: 2rem;
        overflow: visible;
        position: relative;
        width: 90%;
        &.maplegend {
            margin-top: 0.6rem;
            width: 100%;
            z-index: 1;
        }
        svg {
            align-self: center;
            height: 100%;
            overflow: visible;
            width: 100%;
            text {
                fill: $gray;
                font-size: 10px;
                font-weight: 600;
                text-align: right;
            }
        }
    }
    .map-button {
        background: $yellow-light;
        border-radius: 5px;
        color: $orange;
        cursor: pointer;
        font-size: 0.75rem;
        font-weight: 900;
        margin-bottom: 0.25rem;
        padding: 0.5rem;
        position: absolute;
        right: 0;
        transition: all 0.2s ease-in-out;
        &:hover {
            background: $yellow-hover;
        }
        svg {
            height: 12px;
            margin-left: 0.25rem;
            stroke: $orange-mid;
            stroke-width: 3px;
            vertical-align: middle;
            width: 12px;
        }
    }
    .panel {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        min-height: 6rem;
        padding-top: 1rem;
        position: relative;
        h2 {
            font-weight: 900;
            margin-top: 0;
            transition: color 0.3s ease-in-out;
            word-wrap: break-word;
            &.confirmed {
                color: $brick;
            }
            &.active {
                color: $blue;
            }
            &.recovered {
                color: $green;
            }
            &.deceased {
                color: $gray;
            }
        }
        h4 {
            color: $gray-mid;
            margin: 0;
            word-wrap: break-word;
        }
        .panel-left {
            display: flex;
            flex: 1;
            flex-direction: column;
            .district {
                line-height: 15px;
                margin: 0;
                transition: color 0.3s ease-in-out;
                span {
                    font-size: 0.75rem;
                    font-weight: 600;
                }
                &.confirmed {
                    color: $brick;
                }
                &.active {
                    color: $blue;
                }
                &.recovered {
                    color: $green;
                }
                &.deceased {
                    color: $gray;
                }
                &.tested {
                    color: $purple;
                }
            }
        }
        .panel-right {
            display: flex;
            flex-direction: column;
            svg {
                transition: all 0.3s ease-in-out;
            }
            &.is-confirmed {
                svg {
                    fill: $cherry-light;
                    stroke: $cherry-mid;
                }
                .boundary {
                    svg {
                        fill: $cherry-mid;
                        stroke: none;
                    }
                }
                .is-highlighted {
                    background: $cherry-light;
                    &:hover {
                        background: $cherry-hover;
                    }
                }
            }
            &.is-active {
                svg {
                    fill: $blue-light;
                    stroke: $blue-mid;
                }
                .boundary {
                    svg {
                        fill: $blue-mid;
                        stroke: none;
                    }
                }
                .is-highlighted {
                    background: $blue-light;
                    &:hover {
                        background: $blue-hover;
                    }
                }
            }
            &.is-recovered {
                svg {
                    fill: $green-light;
                    stroke: $green-mid;
                }
                .boundary {
                    svg {
                        fill: $green-mid;
                        stroke: none;
                    }
                }
                .is-highlighted {
                    background: $green-light;
                    &:hover {
                        background: $green-hover;
                    }
                }
            }
            &.is-deceased {
                svg {
                    fill: $gray-light;
                    stroke: $gray-mid;
                }
                .boundary {
                    svg {
                        fill: $gray-mid;
                        stroke: none;
                    }
                }
                .is-highlighted {
                    background: $gray-middark;
                    &:hover {
                        background: $gray-hover;
                    }
                }
            }
            .switch-type,
            .switch-statistic {
                display: flex;
                flex-direction: row;
                justify-content: flex-end;
                margin-bottom: .25rem;
                div {
                    align-items: center;
                    background: $gray-light;
                    border-radius: 5px;
                    cursor: pointer;
                    display: flex;
                    flex-direction: row;
                    height: 2.5rem;
                    justify-content: center;
                    width: 2.5rem;
                    &.back {
                        margin-right: 0 !important;
                        svg {
                            fill: $gray;
                            stroke: transparent;
                        }
                    }
                    &:hover {
                        background: $gray-hover;
                    }
                    &:not(:last-child) {
                        margin-right: .25rem;
                    }
                    svg {
                        height: 100%;
                    }
                }
                .divider {
                    border-radius: 5px;
                    width: 2px !important;
                }
            }
            .switch-statistic {
                align-self: flex-end;
                flex-direction: row;
                position: absolute;
                top: 5rem;
                div {
                    background: none;
                    display: flex;
                    flex-direction: row;
                    height: 1.25rem;
                    margin-bottom: .25rem;
                    margin-left: .65rem !important;
                    width: 1.25rem;
                    span {
                        position: absolute;
                    }
                    &:hover {
                        background: none;
                    }
                    &.is-confirmed {
                        svg {
                            fill: $cherry-hover;
                            stroke: none;
                        }
                        &.is-highlighted {
                            svg {
                                fill: $cherry;
                                transform: scale(1.25);
                            }
                        }
                    }
                    &.is-active {
                        svg {
                            fill: $blue-hover;
                            stroke: none;
                        }
                        &.is-highlighted {
                            svg {
                                fill: $blue;
                                transform: scale(1.25);
                            }
                        }
                    }
                    &.is-recovered {
                        svg {
                            fill: $green-hover;
                            stroke: none;
                        }
                        &.is-highlighted {
                            svg {
                                fill: $green;
                                transform: scale(1.25);
                            }
                        }
                    }
                    &.is-deceased {
                        svg {
                            fill: $gray-hover;
                            stroke: none;
                        }
                        &.is-highlighted {
                            svg {
                                fill: $gray;
                                transform: scale(1.25);
                            }
                        }
                    }
                }
            }
        }
        &.bottom {
            margin-top: 0;
            padding-top: 0;
        }
    }
    #chart,
    #legend {
        filter: saturate(1.5);
    }
    .disclaimer {
        align-items: center;
        backdrop-filter: saturate(180%) blur(20px);
        border-radius: 5px;
        bottom: 50%;
        display: flex;
        flex-direction: row;
        font-size: 12px;
        left: 0;
        margin-left: auto;
        margin-right: auto;
        padding: 0.5rem;
        pointer-events: none;
        position: absolute;
        right: 0;
        transition: background, color 0.3s ease-in-out;
        width: 10rem;
        z-index: 99;
        &.is-confirmed {
            background: $cherry-light;
            color: $cherry;
        }
        &.is-active {
            background: $blue-light;
            color: $blue;
        }
        &.is-recovered {
            background: $green-light;
            color: $green;
        }
        &.is-deceased {
            background: $gray-light;
            color: $gray;
        }
        span {
            flex: 1;
        }
        svg {
            align-self: flex-start;
            margin-right: 0.5rem;
            stroke-width: 2px;
            width: 16px;
        }
    }
    .footnote {
        color: $gray;
        margin-bottom: 0;
        margin-top: 1em;
        text-align: left;
        z-index: 1;
        a {
            border-bottom: 2px solid $gray-hover;
            text-decoration: none;
            &:hover {
                background: $gray-hover;
            }
        }
    }
}

.tabs {
    display: flex;
    flex-direction: row;
    position: relative;
    .tab {
        background: $gray-light;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        color: $gray-mid;
        cursor: pointer;
        height: 3.25rem;
        margin-right: 0.25rem;
        min-width: 5rem;
        padding-left: 1rem;
        padding-right: 1rem;
        text-align: center;
        &:hover {
            background: $gray-hover;
        }
        &.focused {
            background: $gray-opaque;
            color: $gray;
        }
    }
}

.tabs-map {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    position: relative;
    z-index: 99;
    h4 {
        display: table-cell;
        font-size: 12px !important;
        font-weight: 900;
        height: 2rem;
        margin-bottom: 0;
        margin-top: 0;
        vertical-align: middle;
    }
    .tab {
        background: $gray-light;
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        color: $gray-mid;
        cursor: pointer;
        height: 2rem;
        margin-right: 0.25rem;
        margin-top: 0.25rem;
        min-width: 1.6rem;
        padding-left: 1rem;
        padding-right: 1rem;
        text-align: center;
        transition: all 0.3s ease-in-out;
        &:hover {
            background: $gray-hover;
        }
        &.focused {
            background: $gray-opaque;
            color: $gray;
        }
        &.disabled {
            background: $gray-opaque;
            color: $gray-mid;
        }
    }
    sup {
        position: relative;
        top: -0.5em;
        vertical-align: top;
    }
}

.TimeseriesExplorer {
    align-self: center;
    display: flex;
    flex-direction: column;
    width: calc(100% - 2rem);
    &.stickied {
        position: sticky !important;
        top: -6.5rem;
    }
    .alert {
        align-self: flex-end;
        margin: 0;
        margin-top: 1rem;
    }
    .state-selection {
        display: flex;
        flex-direction: row;
        margin-top: 1rem;
        max-width: calc(100% - 0.6rem);
        .dropdown {
            min-width: calc(60%);
            select {
                appearance: none;
                background-color: #eee;
                background-image: url('data:image/svg+xml,<svg width="24" height="24" xmlns="http://www.w3.org/2000/svg"><path d="m0,6l12,12l12,-12l-24,0z" fill="gray"/><path fill="none" d="m0,0l24,0l0,24l-24,0l0,-24z"/></svg>');
                background-position-x: calc(100% - 0.4rem);
                background-position-y: 50%;
                background-repeat: no-repeat;
                background-size: 0.6rem;
                border: 2px solid #e8e8e9;
                border-radius: 4px;
                color: $gray;
                cursor: pointer;
                font-weight: 600;
                padding: 0.5rem;
                padding-right: 1.4rem;
                width: 100%;
                &:focus {
                    outline: none;
                }
            }
        }
        .reset-icon {
            margin: auto;
            margin-left: 0.75rem;
            margin-top: 0.5rem;
            svg {
                color: $gray-mid;
                cursor: pointer;
                fill: #a6abb0;
                stroke: #a6abb0;
                stroke-width: 0.5;
                width: 16px;
                &:hover {
                    fill: $gray;
                    stroke: $gray;
                }
            }
        }
    }
}

.Timeseries {
    align-self: center;
    width: 100%;
    .svg-parent {
        height: 10rem;
        margin-bottom: 1rem;
        &.is-confirmed {
            line {
                &.stem {
                    stroke: $cherry-mid;
                }
            }
        }
        &.is-active {
            line {
                &.stem {
                    stroke: $blue-mid;
                }
            }
        }
        &.is-recovered {
            line {
                &.stem {
                    stroke: $green-mid;
                }
            }
        }
        &.is-deceased {
            line {
                &.stem {
                    stroke: $gray-mid;
                }
            }
        }
        &.is-tested {
            line {
                &.stem {
                    stroke: $purple-mid;
                }
            }
        }
    }
}

.timeseries-header {
    align-self: center;
    position: relative;
    width: 100%;
    h1 {
        color: $gray;
        margin: 0;
        margin-bottom: 2rem;
        text-align: center;
    }
    .scale-modes {
        align-items: flex-end;
        bottom: 0;
        color: $gray;
        display: flex;
        flex-direction: row;
        margin-top: 1rem;
        right: 0;
        z-index: 99;
        label {
            color: $gray-mid;
            font-size: 0.75rem;
            font-weight: 900;
            margin-right: 0.25rem;
            z-index: 99;
        }
        &:hover {
            label {
                &.main {
                    color: $gray;
                }
            }
        }
        &>* {
            align-self: center;
        }
    }
    .timeseries-mode,
    .timeseries-logmode {
        display: flex;
        flex-direction: row;
        margin-left: 1rem;
    }
}

.disabled {
    input {
        &.switch {
            background: $gray-light !important;
            border: $gray-light 2px solid !important;
            cursor: not-allowed !important;
            &::after {
                background: $gray-light !important;
            }
        }
    }
}

input {
    &.switch {
        appearance: none;
        background-color: #fff;
        border: 2px solid #d9dadc;
        border-radius: 10px;
        cursor: pointer;
        height: 14px;
        margin: 0;
        outline: none;
        position: relative;
        transition: all 300ms ease-in-out;
        width: 24px;
        &::after {
            background-color: $gray-mid;
            border-radius: 50%;
            content: '';
            height: 10px;
            left: 0;
            position: absolute;
            top: 0;
            transition: all 300ms ease-in-out;
            width: 10px;
        }
        &:checked {
            background-color: $gray-mid;
            transition: all 300ms ease-in-out;
            &::after {
                background-color: $light;
                left: 10px;
                transition: all 300ms ease-in-out;
            }
        }
    }
}

.Timeseries,
.Minigraph {
    display: flex;
    flex-direction: column;
    margin-top: 1rem;
    width: 100%;
    .stats {
        border-radius: 3px;
        display: flex;
        flex-direction: column;
        left: 0.5rem;
        padding: 0.25rem;
        pointer-events: none;
        position: absolute;
        top: 0.5rem;
        h2,
        h5,
        h6 {
            color: $cherry-mid;
            margin: 0;
            transition: all 0.15s ease-in-out;
        }
        h5 {
            &.title {
                color: $cherry-mid;
            }
        }
        h2,
        h6 {
            color: $cherry;
        }
        .stats-bottom {
            display: flex;
            flex-direction: row;
            h6 {
                margin-bottom: 0.1rem;
                margin-left: 0.25rem;
            }
            &>* {
                margin-top: auto;
            }
        }
        &.is-recovered {
            h5 {
                color: $green-mid;
                &.title {
                    color: $green-mid;
                }
            }
            h2,
            h6 {
                color: $green;
            }
        }
        &.is-deceased {
            h5 {
                color: $gray-mid;
                &.title {
                    color: $gray-mid;
                }
            }
            h2,
            h6 {
                color: $gray;
            }
        }
        &.is-active {
            h5 {
                color: $blue-mid;
                &.title {
                    color: $blue-mid;
                }
            }
            h2,
            h6 {
                color: $blue;
            }
        }
        &.is-tested {
            h5 {
                color: $purple-mid;
                &.title {
                    color: $purple-mid;
                }
            }
            h2,
            h6 {
                color: $purple;
            }
        }
    }
    .svg-parent {
        align-self: center;
        background: $cherry-light;
        border-radius: 5px;
        display: flex;
        position: relative;
        width: 100%;
        svg {
            width: 100%;
            .domain,
            .tick {
                stroke: $cherry;
                stroke-width: 1.5;
                line {
                    stroke: $cherry;
                    stroke-width: 1.5;
                }
            }
            text {
                color: $cherry-mid;
                font-size: 9px;
                font-weight: 600;
                stroke: transparent;
            }
        }
        &.is-recovered {
            background: $green-light;
            svg {
                .domain,
                .tick,
                line {
                    stroke: $green;
                }
                text {
                    color: $green-mid;
                    stroke: transparent;
                }
            }
        }
        &.is-deceased {
            background: $gray-light;
            svg {
                .domain,
                .tick,
                line {
                    stroke: $gray;
                }
                text {
                    color: $gray-mid;
                    stroke: transparent;
                }
            }
        }
        &.is-active {
            background: $blue-light;
            svg {
                .domain,
                .tick,
                line {
                    stroke: $blue;
                }
                text {
                    color: $blue-mid;
                    stroke: transparent;
                }
            }
        }
        &.is-tested {
            background: $purple-light;
            svg {
                .domain,
                .tick,
                line {
                    stroke: $purple;
                }
                text {
                    color: $purple-mid;
                    stroke: transparent;
                }
            }
        }
    }
}

.Minigraph {
    align-self: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0;
    margin-bottom: 1rem;
    margin-top: 1rem;
    .svg-parent {
        background: transparent !important;
        width: calc(25%);
    }
}

.floating-buttons {
    bottom: 1rem;
    display: flex;
    flex-direction: column;
    padding: 0;
    position: fixed;
    right: 1rem;
    width: 3rem;
    z-index: 1;
    button {
        backdrop-filter: saturate(180%) blur(2px);
        background: $yellow-mid;
        border: 0;
        border-radius: 50%;
        color: $orange;
        cursor: pointer;
        display: flex;
        flex-direction: row;
        margin: 0.25rem;
        outline: none;
        padding: 0.75rem;
        transition: all 0.2s ease-in-out;
        &:hover {
            background: #ffa500;
            svg {
                stroke: #ffa500;
            }
        }
        svg {
            align-self: center;
            stroke: $orange;
        }
    }
}

.Links {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.link {
    align-self: center;
    margin-bottom: 1rem;
    margin-top: 1rem;
    width: 34rem;
    a {
        background: $blue-light;
        color: $blue;
        font-weight: 900;
        text-decoration: none;
        text-transform: uppercase;
        transition: background 0.2s ease-in-out;
        word-wrap: break-word;
        &:hover {
            background: $blue-hover;
        }
    }
    h3 {
        color: $gray-dark;
        font-weight: 900;
        margin-bottom: 0;
    }
}

footer {
    display: flex;
    flex-direction: column;
    margin-bottom: 4rem;
    margin-top: 4rem;
    width: 100%;
