        color: $gray;
        font-weight: 600;
        margin: 0;
        margin-bottom: 2rem;
    }
    svg {
        margin-right: .5rem;
        padding: .25rem;
    }
    .youtube {
        color: $red-mid;
        &:hover {
            color: $red;
        }
    }
    .github {
        color: $gray-mid;
        &:hover {
            color: $gray;
        }
    }
    .instagram {
        color: $pblue-mid;
        &:hover {
            color: $pblue;
        }
    }
    .website {
        color: $yellow-mid;
        &:hover {
            color: $yellow;
        }
    }
    .mail {
        color: $brown-mid;
        &:hover {
            color: $brown;
        }
    }
}

.About {
    display: flex;
    flex-direction: column;
    min-height: 30rem;
    width: 100%;
    &>* {
        align-self: center;
        margin-bottom: 1.5rem;
        width: 34rem;
    }
    &>*:first-child {
        margin-top: 4rem;
    }
    h2 {
        font-weight: 600;
        margin: 0;
        text-transform: none;
    }
    .question {
        color: $gray-dark;
        margin: 0;
        margin-bottom: 0.25rem;
    }
    .answer {
        color: $blue;
        line-height: 1.5rem;
    }
    a {
        background: $blue-light;
        color: $blue;
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
    }
}

.pills {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    >button {
        background-color: $yellow-light;
        border: 2px $yellow-light solid;
        border-bottom: 0;
        border-top: 0;
        color: $orange;
        cursor: pointer;
        font-size: 10px !important;
        font-weight: 600;
        margin: 0;
        outline: none;
        padding: 10px 15px;
        text-align: center;
        transition: background 0.25s ease-in-out;
        &:hover {
            background: $yellow-hover;
        }
        &.selected {
            background-color: $yellow-mid;
        }
        &:focus {
            &:not(:focus-visible) {
                outline: none;
            }
        }
        &:first-of-type {
            border: 0;
            border-bottom-left-radius: 5px;
            border-top-left-radius: 5px;
        }
        &:last-of-type {
            border: 0;
            border-bottom-right-radius: 5px;
            border-top-right-radius: 5px;
        }
    }
}

.updates-header {
    align-self: center;
    color: $gray-dark;
    width: 100%;
    h2 {
        color: $pblue;
        font-weight: 900;
    }
    h3 {
        color: $blue-mid;
        font-weight: 900;
        &:hover {
            svg {
                color: $blue;
            }
        }
        svg {
            height: 1rem;
        }
    }
}

.updates-footer {
    align-self: center;
    color: $gray-dark;
    margin-left: auto;
    .telegram {
        color: $blue;
        &:hover {
            color: $purple;
        }
    }
    h4 {
        color: $blue-mid;
        font-weight: 900;
        margin-top: .5rem;
        &:hover {
            color: $blue;
        }
        svg {
            height: 1rem;
            margin-bottom: -.25rem;
        }
    }
}

.updates {
    align-self: center;
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
    min-height: 22rem !important;
    width: 100%;
    .update {
        background: $gray-light;
        border-radius: 5px;
        margin-bottom: 0.5rem;
        padding: 0.75rem;
        transition: all 0.2s ease-in-out;
        &:hover {
            background: $gray-hover;
        }
        h5,
        h4 {
            margin: 0;
        }
        h5 {
            color: $gray-mid;
            margin-bottom: 0.15rem;
        }
        h4 {
            color: $gray;
            font-weight: 600;
        }
    }
    .button {
        background: $pblue-light;
        color: $pblue;
        display: flex;
        flex-direction: row;
        height: 3rem;
        width: 13rem;
        &>* {
            align-self: center;
        }
        &:hover {
            background: $pblue-hover;
        }
    }
    a {
        color: inherit;
        display: flex;
        flex-direction: row;
        text-decoration: none;
        &>* {
            align-self: center;
        }
    }
}

.district-bar {
    align-self: center;
    display: flex;
    flex-direction: column;
    .district-bar-bottom {
        .button {
            background: $gray-light;
            color: $gray;
            flex-basis: 15%;
            font-weight: 600;
            height: 2.5rem;
            justify-content: center;
            margin-top: 0.75rem;
            text-transform: unset;
            width: 5rem;
            &:hover {
                background: $gray-hover;
            }
        }
    }
    .district-bar-top {
        align-self: center;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 100%;
        h2 {
            font-weight: 900;
            margin-bottom: 0.5rem;
            margin-top: 0;
            &.confirmed {
                color: $red;
            }
            &.active {
                color: $blue;
            }
            &.recovered {
                color: $green;
            }
            &.deceased {
                color: $gray;
            }
        }
    }
    .district-bar-left {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
    }
    .district-bar-right {
        display: flex;
        flex-basis: 15rem;
        flex-grow: 1;
        justify-content: center;
        position: relative;
        .happy-sign {
            display: flex;
            flex-direction: row;
            position: absolute;
            top: 4rem;
        }
        .DeltaBarGraph {
            margin-top: 1rem;
            width: 100%;
            svg {
                width: 100%;
                .label {
                    font-size: 11px;
                    font-weight: 900;
                    text-anchor: middle;
                }
            }
        }
    }
    .districts {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        width: 10rem;
        &.is-grid {
            display: grid;
            grid-auto-flow: column;
            grid-gap: 0.5rem;
            padding-bottom: 0.5rem;
            width: 100%;
        }
        .district {
            display: flex;
            flex-direction: row;
            height: 2.5rem;
            justify-content: flex-start;
            margin-right: 0.5rem;
            h2,
            h5 {
                color: $gray;
                font-weight: 600;
                margin: 0;
            }
            h5 {
                display: inline-block !important;
                margin-left: 0.5rem;
                margin-top: 0.15rem;
                width: auto !important;
            }
            &>* {
                align-self: center;
            }
            .delta {
                display: flex;
                flex-direction: row;
                margin-left: 0.5rem;
                margin-top: 4px;
                h6 {
                    margin: 0;
                    &.confirmed {
                        color: $red-mid;
                    }
                    &.active {
                        color: $blue-mid;
                    }
                    &.recovered {
                        color: $green-mid;
                    }
                    &.deceased {
                        color: $gray-mid;
                    }
                }
                svg {
                    stroke-width: 4;
                    vertical-align: bottom;
                    width: 10px;
                    &.confirmed {
                        color: $red-mid;
                    }
                    &.active {
                        color: $blue-mid;
                    }
                    &.recovered {
                        color: $green-mid;
                    }
                    &.deceased {
                        color: $gray-mid;
                    }
                }
                &>* {
                    align-self: center;
                }
            }
