.SunMoon {
    cursor: pointer;
}

.Tooltip {
    display: flex;
    flex-direction: column;
    .message {
        background: #000;
        border-radius: 5px;
        display: flex;
        flex-direction: row;
        font-size: 13px;
        font-weight: 400;
        justify-content: flex-end;
        max-width: 30vw;
        min-width: 10rem;
        padding: .5rem;
        pointer-events: none;
        right: 0;
        top: 0;
        transform: translate3d(2rem, 0, 0);
        p {
            color: $white !important;
            margin: 0;
        }
    }
}

@media (min-width: 769px) {
    .Home,
    .State {
        padding-top: 5rem;
    }
    .Navbar {
        background: $gray-light-opaque;
        flex-direction: column;
        height: 100%;
        justify-content: flex-start;
        min-width: 5rem;
        position: fixed;
        z-index: 999;
        .navbar-left {
            margin-bottom: .5rem;
            margin-top: auto;
            order: 3;
            text-align: center;
        }
        .navbar-middle {
            font-size: 0.6rem;
            order: 1;
            padding-bottom: 2rem;
            padding-top: 2rem;
            text-align: center;
            width: 46px;
            word-wrap: break-word;
        }
        .navbar-right {
            align-items: center;
            background: $gray-light-opaque;
            display: flex;
            flex-direction: column;
            order: 2;
            padding-bottom: 0;
            padding-top: 0;
            span {
                display: flex;
                flex-direction: row;
                height: 1.5rem;
                justify-content: center;
                margin-bottom: 1.5rem;
                margin-top: 1.5rem;
                &:hover {
                    color: $gray-mid;
                }
            }
        }
        .expand {
            background: $gray-light-opaque;
            flex-direction: column;
            height: calc(100% - 5.5rem);
            padding-left: 0;
            padding-top: 5.75rem;
            top: 0;
            width: 15rem;
            &>* {
                border: 0;
                color: $gray;
                font-weight: 600;
                height: 1.5rem;
                width: 12rem;
            }
        }
    }
    .dark-mode {
        .Navbar,
        .expand,
        .navbar-right {
            background: #1e1e30 !important;
        }
    }
    .Home {
        .home-left,
        .home-right {
            &.expanded {
                width: 73rem;
            }
        }
        .home-left {
            &.expanded {
                margin-bottom: 2.5rem;
                margin-right: 0;
            }
        }
        .home-right {
            &.expanded {
                flex-direction: row;
                margin-left: 0;
            }
        }
    }
    .map-container {
        &.expanded {
            margin-right: 2.5rem;
            width: 50%;
        }
    }
    .TimeseriesExplorer {
        &.expanded {
            margin-left: 2.5rem;
            width: calc(50% - 2rem);
        }
    }
}

@media (max-width: 769px) {
    .table-container,
    .header,
    .TimeseriesExplorer,
    .map-container,
    .Links,
    .Summary,
    .About,
    .updates,
    .updates-header,
    .StateMeta,
    .timeline,
    .table-top,
    .LanguageSwitcher {
        width: calc(100% - 2rem);
    }
    .Home,
    .State {
        display: flex;
        flex-direction: column;
        margin-left: 0;
        margin-right: 0;
        padding-top: 1rem;
        width: 100%;
        .home-left,
        .home-right,
        .state-left,
        .state-right {
            display: flex;
            flex-direction: column;
            margin: 0;
            width: 100%;
        }
    }
    .Level {
        h1 {
            font-size: 5vw !important;
        }
    }
    .map-container {
        align-self: center;
    }
    .MapExplorer {
        .svg-parent {
            width: 100%;
        }
        .header {
            margin-top: 2rem;
        }
    }
    .link,
    .faq {
        width: calc(100% - 2rem);
    }
    .Timeseries {
        .svg-parent {
            width: 100%;
        }
    }
    .Minigraph {
        .svg-parent {
            padding: 0;
        }
    }
    .map-stats {
        .stats {
            .timestamp {
                width: 4.5rem;
            }
        }
    }
    a {
        &.button {
            display: flex;
            flex: row;
            justify-content: center;
            width: 10rem;
            &>* {
                align-self: center;
            }
            svg {
                width: 38px;
            }
        }
    }
    .Banner {
        height: 7rem;
        .snippet {
            margin-left: 1rem;
            margin-right: 1rem;
            width: 100%;
        }
    }
    .cards-container {
        padding: .5rem;
        .cards {
            .card {
                flex: 0 1 100%;
                height: 25rem;
                margin-bottom: 0;
            }
            .chart-title {
                padding: 20px 0 10px;
            }
            .card-big {
                height: 30rem;
            }
        }
    }
    .Demographics {
        margin-left: 0;
        width: 100%;
        .select {
            .react-date-picker__calendar {
                position: fixed;
                top: 2.3rem !important;
            }
        }
        .header {
            margin: 0;
            padding-left: 1rem;
            padding-right: 1rem;
            width: calc(100% - 2rem);
        }
        .reminder {
            padding-bottom: 1rem;
            padding-top: 1rem;
            width: calc(100% - 4rem);
        }
        .Patients {
            width: calc(100% - 2rem);
        }
        .modal {
            .modal-content {
                width: calc(100% - 5rem);
                .meta {
                    height: 20rem;
                }
            }
        }
        .filters-left {
            flex-wrap: wrap;
            justify-content: space-between;
        }
        .filters {
            align-self: center;
            height: auto;
            justify-content: center;
            padding: 0.5rem;
        }
    }
    .patients-summary {
        width: calc(100% - 2rem);
        .modal {
            .modal-content {
                width: calc(100% - 5rem);
                .meta {
                    height: 20rem;
                }
            }
        }
    }
    .patientdb-wrapper {
        padding-left: 1rem !important;
        padding-right: 1rem !important;
        .Patients {
            width: 100%;
        }
        .day {
            justify-content: center;
            width: 100%;
        }
    }
    &.nationality {
        margin-bottom: 0.5rem;
    }
    .Navbar {
        .expand {
            height: calc(100% - 4rem);
            overflow: auto;
            transform: none !important;
        }
        .expand-bottom {
            margin-top: auto;
        }
        .navbar-right {
            span {
                display: none;
                &:first-child {
                    display: initial;
                }
            }
        }
    }
    .Search {
        .result-name {
            max-width: 10rem !important;
        }
    }
    .State {
        .MapExplorer,
        .StateHeader {
            width: calc(100% - 2rem);
        }
        .district-bar {
            width: calc(100% - 3rem);
            h1 {
                padding-left: 0;
            }
        }
    }
    .StateMeta {
        display: grid;
        grid-gap: 1rem;
        grid-template-rows: repeat(auto-fit, minmax(5rem));
        margin-bottom: 1rem;
        .meta-item {
            min-width: 1rem;
        }
    }
    .timeline {
        padding-left: 0 !important;
        padding-right: 0 !important;
    }
    .Timeline {
        width: 100%;
    }
    .LanguageSwitcher {
        margin-left: 0;
    }
}

// Dark Mode
.dark-mode {
    background: #161625 !important;
    color: #bdbdbd;
    .switch-wrapper>div {
        background: #161625 !important;
    }
    .is-tested {
        color: $dark-m-purple !important;
        svg {
            color: $dark-m-purple !important;
        }
    }
    .expand {
        background: #1e1e30 !important;
    }
    .Navbar,
    .expand {
        border-right: 0;
        .navbar-middle {
            color: #bdbdbd;
        }
    }
    .table {
        .cell {
            &:first-child {
                background: #1e1e30 !important;
            }
            &.heading {
                background: #1e1e30 !important;
                &:hover {
                    background: #212130 !important;
                }
            }
        }
        .is-highlighted,
        .is-total {
            .cell {
                background: #1e1e30 !important;
            }
        }
    }
    .Search {
        input {
            background: #1e1e30 !important;
        }
    }
    .State {
        .header-right {
            h5 {
                color: #9673b9bb !important;
            }
            h2 {
                color: #9673b9 !important;
            }
            a {
                background: $dark-m-purple-light !important;
            }
        }
        .breadcrumb {
            ul {
                background: #1e1e30 !important;
                &::after {
                    border-bottom-color: #1e1e30 !important;
                }
                li {
                    a {
                        color: $gray !important;
                        &:hover {
                            color: $white !important;
                        }
                    }
                }
            }
        }
    }
    .StateHeader {
        .header-right {
            color: $dark-m-purple-mid;
            h2 {
                color: $dark-m-purple;
            }
            a {
                background: $dark-m-purple-light;
            }
        }
    }
    .MapExplorer {
        #chart,
        #legend {
            &.zone {
                filter: none;
            }
            &:not(.zone) {
                filter: invert(1) hue-rotate(180deg) saturate(1.5) url('#balance-color');
            }
        }
        .maplegend {
            background: #161625 !important;
        }
        .meta {
            h1 {
                &.tested {
                    color: #9673b9 !important;
                }
            }
            h2 {
                &.tested {
                    color: #9673b9 !important;
                }
            }
        }
        .stats {
            &.tested {
                background: $dark-m-purple-light !important;
                h1 {
                    color: #9673b9 !important;
                }
                h5,
                h6 {
                    color: #9673b9bb !important;
                }
                svg {
                    stroke: #9673b9bb !important;
                    &:hover {
                        stroke: #9673b9 !important;
                    }
                }
            }
        }
        .disclaimer {
            color: $gray-light-opaque;
        }
    }
    .Timeseries,
    .Minigraph {
        .stats {
            &.is-tested {
                h5 {
                    color: $dark-m-purple-mid !important;
                    &.title {
                        color: $dark-m-purple-mid !important;
                    }
                }
                h2,
                h6 {
                    color: $dark-m-purple !important;
                }
            }
        }
        .svg-parent {
            &.is-tested {
                background: $dark-m-purple-light !important;
                svg {
                    circle,
                    .domain,
                    .tick {
                        stroke: $dark-m-purple !important;
                    }
                    circle {
                        fill: $dark-m-purple !important;
                    }
                    .trend,
                    line {
                        stroke: #9673b999 !important;
                    }
                    text {
                        color: $dark-m-purple-mid !important;
                        stroke: transparent;
                    }
                }
            }
        }
    }
    .tpm {
        background: $dark-m-purple-light !important;
        h3,
        h5,
        svg,
        p {
            color: #9673b9bb !important;
        }
        h1 {
            color: #9673b9 !important;
        }
    }
    .tab {
        &.focused {
            background: #1e1e30 !important;
            color: #bdbdbd !important;
        }
    }
    .pills {
        button {
            &.selected {
                background: #{$yellow}50;
            }
        }
    }
    .TimeseriesExplorer {
        .state-selection {
            .dropdown {
                select {
                    background: #1e1e30;
                    background-image: linear-gradient(45deg, transparent 50%, $gray 50%), linear-gradient(135deg, $gray 50%, transparent 50%);
                    background-position: calc(100% - 13px) 50%, calc(100% - 8px) 50%;
                    background-repeat: no-repeat;
                    background-size: 5px 5px, 5px 5px;
                    border-color: #9494941a;
                }
            }
        }
    }
    input {
        &.switch {
            background-color: #161625 !important;
            border: 2px solid $gray-mid !important;
            &:checked {
                background-color: #6c757d99 !important;
                &::after {
                    background-color: #cfcfcf !important;
                }
            }
            &::after {
                background-color: #cfcfcf !important;
            }
        }
    }
    .disabled {
        input {
            &.switch {
                background: $dark !important;
                border: $gray-light 2px solid !important;
                opacity: 0.7;
                &::after {
                    background: $gray !important;
                }
            }
        }
    }
    .About {
        .question {
            color: $gray !important;
        }
    }
    .Demographics {
        .filters {
            background: #1e1e30 !important;
            box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.12), inset 0 -1px 0 0 #1e1e30;
        }
        .legend {
            background: #1e1e30 !important;
        }
        .react-date-picker {
            background: #1e1e30;
            border-color: #9494941a !important;
        }
        .modal-content {
            background: #1e1e30 !important;
        }
        .modal {
            background: #1e1e30bb !important;
        }
    }
    .StateDropdown {
        .dropdown {
            background: #1e1e30 !important;
        }
    }
    .TableLoader {
        svg {
            rect {
                fill: #1e1e30 !important;
            }
        }
    }
}

// Animation Support
.fadeInUp {
    animation-duration: 0.45s;
    animation-fill-mode: both;
    animation-name: fadeInUp;
}

@keyframes fadeInUp {
    0% {
        opacity: 0;
        transform: translateY(20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.rotateDownRight {
    animation-duration: 0.25s;
    animation-fill-mode: both;
    animation-name: rotateDownRight;
}

@keyframes rotateDownRight {
    100% {
        transform: rotate(-90deg);
    }
}

.rotateRightDown {
    animation-duration: 0.25s;
    animation-fill-mode: both;
    animation-name: rotateRightDown;
}

@keyframes rotateRightDown {
    0% {
        transform: rotate(-90deg);
    }
}

.fadeOutDown {
    animation-duration: 0.45s;
    animation-fill-mode: both;
    animation-name: fadeOutDown;
}

@keyframes fadeOutDown {
    0% {
        opacity: 1;
        transform: translateY(0);
    }
    100% {
        opacity: 0;
        transform: translateY(20px);
    }
}